import React from "react"

import "../styles/menu.scss"

const MenuScreen = () => {
    return (
        <>
            <div className='grid menu-screen'>
                <h1 className='center-text col-12'>Menus</h1>
                <div className='menu col-12'>
                    <div className='menu-header'>
                        <h2>Lunch</h2>
                        <p>served from 11:00am to 3:00pm everyday</p>
                    </div>
                </div>
            </div>
        </>
    )
}

export default MenuScreen
